@keyframes moveIn {
    from {
        margin-left: -37%;
        transform: scale(0.2);
        opacity: 0.5;
    }
    to {
        margin-left: 0;
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes moveOut {
    from {
        margin-left: 0;
        transform: scale(1);
        opacity: 1;
    }
    to {
        margin-left: -37%;
        transform: scale(0.2);
        opacity: 0.5;
    }
}

.carousel--item {
    width: 50%;
    height: 300px;
    /*text-align: center;*/
    margin: 0 0 0 -37%;
    padding: 0 0 0 20px;
    transform: scale(0.2);
    /*transition: transform 300ms;*/
    opacity: 0.5;
    outline-style: none;
    display: flex !important;
    -webkit-animation : moveOut 1s;
    -moz-animation    : moveOut 1s;
    -o-animation      : moveOut 1s;
    animation         : moveOut 1s;
    /*align-items: center;*/
}

.active--item {
    margin-left: 0;
    transform: scale(1);
    opacity: 1;
    -webkit-animation : moveIn 1s;
    -moz-animation    : moveIn 1s;
    -o-animation      : moveIn 1s;
    animation         : moveIn 1s;
}

