.carousel--item--preview--cont {
    position: relative;
}

.carousel--item--preview {
    filter: drop-shadow(0 0 1.5rem transparent);
    transition: filter .3s ease-in-out;
}

.carousel--item--shadow {
    filter: blur(.5rem);
    z-index: -1;
    position: absolute;
    /*top: 2.8em;*/
    /*top: 0;*/
    left: 0;
    width: 100%;
    top: 50%;
    transform: translateY(-50%)
}

