.info--container {
    /*background: rebeccapurple;*/
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.carousel--item ul {
    text-align: left;
}

.item--description {
    font-weight: 700;
    color: #d4d7c5 !important;
    text-align: center;
    margin-bottom: 0.5em !important;
    font-size: 1.2em;
}

.item--caption {
    text-align: center;
    margin-top: 0 !important;
    margin-bottom: 1.5em !important;
}

.projects--title {
    text-align: center;
    /*margin-top: 10%;*/
}

.item--buttons--container {
    display: flex;
    justify-content: center;
}

.carousel--button {
    width: 40%;
    margin: 0 0.7em;
}

/* Light theme */

.light .carousel--item h4:hover {
    text-decoration: underline;
}

.light .carousel--item:hover .carousel--item--preview {
    filter: drop-shadow(0 0 1rem darkgray);
    transition: filter .5s;
}

.light .carousel--item:hover .carousel--item--shadow {
    filter: blur(0);
    transition: filter .5s;
}

.light .carousel--item--shadow {
    filter: blur(.75rem);
    transition: filter .5s;
}

.light .item--description {
    color: #2B283A !important;
}

